body,
html {
  background-color: #eeeeee;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
.clear:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.pos-r {
  position: relative;
}
.hide {
  display: none;
}
a,
a:hover {
  text-decoration: none;
}
header {
  position: relative;
  padding: 0.4rem;
  background: linear-gradient(to bottom, #1BB0FC 0%, #25C3FA 100%);
}
header h2 {
  text-align: center;
  font-size: 18px;
  color: #fff;
}
[data-dpr="2"] header h2 {
  font-size: 36px;
}
[data-dpr="3"] header h2 {
  font-size: 54px;
}
header .arrow {
  position: absolute;
  top: 50%;
  left: 0.26666667rem;
  width: 0.26666667rem;
  height: 0.26666667rem;
  transform-origin: 50% 50%;
  transform: translateY(-50%) rotate(45deg);
  border-left: 2px solid #fff;
  border-bottom: 2px solid #fff;
}
body,
html {
  background-color: #fff;
}
nav {
  display: flex;
  background-color: #f7f7f7;
  padding: 0.13333333rem 0;
}
nav li {
  list-style-type: none;
  padding: 0.26666667rem 0;
  flex: 1;
  border-right: 1px solid #e8e8e8;
  text-align: center;
  color: #333;
  font-size: 14px;
}
[data-dpr="2"] nav li {
  font-size: 28px;
}
[data-dpr="3"] nav li {
  font-size: 42px;
}
nav li.on {
  color: #09f;
}
nav li:last-child {
  border: none;
}
.newcartitle {
  font-size: 14px;
  padding: 1.06666667rem 0 0.53333333rem 0;
  color: #f60;
  text-align: center;
}
[data-dpr="2"] .newcartitle {
  font-size: 28px;
}
[data-dpr="3"] .newcartitle {
  font-size: 42px;
}
footer {
  position: fixed;
  left: 0;
  bottom: 0.26666667rem;
  width: 100%;
  font-size: 14px;
}
[data-dpr="2"] footer {
  font-size: 28px;
}
[data-dpr="3"] footer {
  font-size: 42px;
}
footer a {
  width: 46%;
  display: inline-block;
  margin-left: 2%;
  text-align: center;
  background-color: #09f;
  border-radius: 0.13333333rem;
  padding: 0.4rem 0;
  color: #fff;
}
footer a:last-child {
  margin-right: 2%;
}
footer.daqu a {
  width: 80%;
  display: inline-block;
  margin: 0 10%;
  text-align: center;
  background-color: #09f;
  border-radius: 0.13333333rem;
  padding: 0.4rem 0;
  color: #fff;
}
.qu-data {
  background-color: #94D6FF;
  height: 1.6rem;
  width: 100%;
  color: #fff;
}
.qu-data .swiper-button-prev {
  background: none;
  width: 0;
  height: 0;
  border-top: 0.21333333rem solid transparent;
  border-right: 0.26666667rem solid #fff;
  border-bottom: 0.21333333rem solid transparent;
  margin-top: 0;
  left: 0.26666667rem;
  transform: translateY(-50%);
}
.qu-data .swiper-button-next {
  background: none;
  width: 0;
  height: 0;
  border-top: 0.21333333rem solid transparent;
  border-left: 0.26666667rem solid #fff;
  border-bottom: 0.21333333rem solid transparent;
  margin-top: 0;
  right: 0.26666667rem;
  transform: translateY(-50%);
}
.qu-data .swiper-slide {
  text-align: center;
  font-size: 16px;
  line-height: 1.6rem;
}
[data-dpr="2"] .qu-data .swiper-slide {
  font-size: 32px;
}
[data-dpr="3"] .qu-data .swiper-slide {
  font-size: 48px;
}
.qu-p {
  padding: 0.61333333rem 0;
  text-align: center;
  font-size: 12px;
  color: #f60;
}
[data-dpr="2"] .qu-p {
  font-size: 24px;
}
[data-dpr="3"] .qu-p {
  font-size: 36px;
}
.qu-p i {
  width: 0.4rem;
  height: 0.4rem;
  background: url('../../img/ji2.png') no-repeat;
  background-size: contain;
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.13333333rem;
}
.qu-riqi {
  width: 100%;
}
.qu-riqi .swiper-slide {
  text-align: center;
  font-size: 16px;
  padding-top: 0.13333333rem;
  height: 0.66666667rem;
}
[data-dpr="2"] .qu-riqi .swiper-slide {
  font-size: 32px;
}
[data-dpr="3"] .qu-riqi .swiper-slide {
  font-size: 48px;
}
.qu-riqi .swiper-slide.swiper-active span {
  border: 1px solid #1DB5FB;
  background-color: #94D6FF;
  border-radius: 0.13333333rem;
  color: #fff;
}
.qu-riqi .swiper-slide.swiper-active span:after {
  content: '';
}
.qu-riqi .swiper-slide.hui span {
  color: #eee;
}
.qu-riqi .swiper-slide.hui span:after {
  content: '';
}
.qu-riqi span {
  position: relative;
  padding: 0.10666667rem 0.21333333rem;
  color: #666;
  margin: 0 auto;
}
.qu-riqi span:after {
  position: absolute;
  content: '•';
  bottom: -30%;
  left: 50%;
  transform: translateX(-50%);
  color: #94D6FF;
}
.qu-xingqi {
  display: flex;
  font-size: 16px;
}
[data-dpr="2"] .qu-xingqi {
  font-size: 32px;
}
[data-dpr="3"] .qu-xingqi {
  font-size: 48px;
}
.qu-xingqi span {
  flex: 1;
  text-align: center;
  color: #30B4FF;
  padding-top: 0.53333333rem;
  padding-bottom: 0.4rem;
}
.qu-yue .swiper-slide {
  padding-top: 0.4rem;
}
